<template>
  <main class="catalog_body">
    <div class="catalog_product_body">
      <header class="product_type">
        <ul>
          <li
            v-for="pType in productType"
            :key="pType.name"
            @click="selectType(pType)"
            :class="{ selected: pType.selected }"
          >
            <a class="catalog_button"> {{ pType.name }} </a>
          </li>
        </ul>

        <div class="close_box">
          <img
            @click="$emit('close')"
            src="./../../assets/images/interface/close.png"
            alt=""
          />
        </div>
      </header>
      <nav class="sort">
        <product-sorting
          :key="type"
          :type="type"
          @sorting="sortProducts"
        ></product-sorting>
      </nav>
      <div v-if="catalogMode" class="catalog_product_list">
        <article
          v-for="product in productList"
          :key="product.id"
          @click="openPreview(product.id)"
          class="catalog_product"
          :class="{ mini: !product.src }"
        >
          <img v-if="product.src" :src="$images + product.src" alt="" />
          <h4>{{ product.name }}</h4>
          <button
            v-if="previewAdd"
            class="add"
            @click.stop="$emit('addProduct', product.id, type)"
          >
            
          </button>
        </article>
      </div>
      <div v-else class="warehouse_product_list">
        <table>
          <tr>
            <th></th>
            <th>{{ type == "Linoleum" ? "" : "-" }}</th>
            <th></th>
          </tr>
          <tr v-for="item in productList" :key="item.id">
            <td>{{ item.name }}</td>
            <td>{{ type == "Linoleum" ? item.size : item.col_vo }}</td>
            <td>
              <button
                v-if="previewAdd"
                class="add"
                @click.stop="$emit('addProductFromWarehouse', item.id, type)"
              >
                
              </button>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div v-show="preview" class="catalog_preview_product">
      <template v-if="preview">
        <header>
          <button @click="closePreview" class="back"></button>
          <h2>{{ previewProduct.name }}</h2>
        </header>
        <div class="slider">
          <div class="slider_body">
            <div v-for="src in previewProduct.images" :key="src" class="item">
              <img :src="$images + src" alt="" />
            </div>
          </div>
        </div>
        <div v-if="type == 'Linoleum'" class="list">
          <div class="column">
            <h5></h5>
            <div class="li"> : {{ previewProduct.class }}</div>
            <div class="li">
               ,: {{ previewProduct.totalThickness }}
            </div>
            <div class="li"> : {{ previewProduct.type }}</div>
            <div class="li">
                : {{ previewProduct.protective }}
            </div>
            <div class="li"> : {{ previewProduct.base }}</div>
          </div>
          <div class="column">
            <h5> </h5>
            <div class="li">: {{ previewProduct.maker }}</div>
            <div class="li">: {{ previewProduct.collection }}</div>
            <div class="li">
              : {{ previewProduct.destination }}
            </div>
            <div class="li">
               :
              <span v-for="width in previewProduct.width" :key="width">
                {{ width }}</span
              >
            </div>
          </div>
          <div class="column">
            <h5> </h5>
            <div class="li">
              <input
                type="number"
                v-model="s"
                @keydown.enter="addPrice(s)"
                placeholder=" "
              />
              <button @click="addPrice(s)"> </button>
            </div>
            <div class="li">: {{ previewProduct.price.selected }}</div>
            <div class="li">
              <button
                v-if="previewAdd"
                class="add"
                @click.stop="$emit('addProduct', previewProduct.id, type)"
              >
                
              </button>
            </div>
          </div>
        </div>
        <div v-if="type == 'Plintusa'" class="list">
          <div class="column">
            <h5></h5>
            <div class="li">: {{ previewProduct.maker }}</div>
            <div class="li">: {{ previewProduct.collection }}</div>
            <div class="li">: {{ previewProduct.width }}</div>
            <div class="li">: {{ previewProduct.heigth }}</div>
            <div class="li">: {{ previewProduct.len }}</div>
          </div>
          <div class="column">
            <h5></h5>
            <div class="li">{{ previewProduct.price }} /</div>
            <div class="li">
              <button
                v-if="previewAdd"
                class="add"
                @click.stop="$emit('addProduct', previewProduct.id, type)"
              >
                
              </button>
            </div>
          </div>
        </div>
        <div v-if="type == 'Linoleum'" class="recomendation">
          <h2> </h2>
          <article
            v-for="plinth in previewProduct['rec']"
            @click="
              type = 'Plintusa';
              openPreview(plinth.id);
            "
            :key="plinth.id"
          >
            <img :src="$images + plinth.src" alt="" />
            <h4> {{ plinth.name }}</h4>
          </article>
        </div>
        <div v-if="type == 'Plintusa'" class="recomendation">
          <h2></h2>
          <article
            style="text-align: center; height: 15vh"
            v-for="accessories in previewProduct['accessories']"
            @click="
              type = 'Plintusa';
              openPreview(accessories.id);
            "
            :key="accessories.id"
          >
            <h4>{{ accessories.name }}</h4>
            <button
              v-if="previewAdd"
              class="add"
              @click.stop="$emit('addProduct', accessories.id, 'accessories')"
            >
              
            </button>
          </article>
        </div>
      </template>
    </div>
  </main>
</template>
<script>
function translit(word) {
  var answer = "";
  var converter = {
    : "a",
    : "b",
    : "v",
    : "g",
    : "d",
    : "e",
    : "e",
    : "zh",
    : "z",
    : "i",
    : "y",
    : "k",
    : "l",
    : "m",
    : "n",
    : "o",
    : "p",
    : "r",
    : "s",
    : "t",
    : "u",
    : "f",
    : "h",
    : "c",
    : "ch",
    : "sh",
    : "sch",
    : "",
    : "y",
    : "",
    : "e",
    : "yu",
    : "ya",

    : "A",
    : "B",
    : "V",
    : "G",
    : "D",
    : "E",
    : "E",
    : "Zh",
    : "Z",
    : "I",
    : "Y",
    : "K",
    : "L",
    : "M",
    : "N",
    : "O",
    : "P",
    : "R",
    : "S",
    : "T",
    : "U",
    : "F",
    : "H",
    : "C",
    : "Ch",
    : "Sh",
    : "Sch",
    : "",
    : "Y",
    : "",
    : "E",
    : "Yu",
    : "Ya",
  };

  for (var i = 0; i < word.length; ++i) {
    if (converter[word[i]] == undefined) {
      answer += word[i];
    } else {
      answer += converter[word[i]];
    }
  }

  return answer;
}
import productSorting from "./productSorting.vue";
export default {
  components: {
    productSorting,
  },
  data() {
    return {
      s: "",
      preview: false,
      previewAdd: false,
      catalogMode: true,
      type: translit(""),
      productType: [
        { name: "", selected: true },
        { name: "", selected: false },
        { name: "", selected: false },
        { name: ". ", selected: false },
      ],
      productList: [],
      previewProduct: {},
    };
  },
  methods: {
    async sortProducts(data) {
      this.productList = [];
      data.type = this.type;
      data.warehouse = this.catalogMode;
      this.productList = await fetch(
        this.$connect + "catalog/productList.php",
        {
          method: "POST",
          body: JSON.stringify(data),
        }
      ).then((resp) => resp.json());
    },
    async selectType(type) {
      if (type.selected) {
        return false;
      }
      this.productType.forEach((el) => {
        el.selected = false;
      });
      type.selected = true;

      this.type = this.productType.filter((type) => type.selected == true)[0];
      this.type = translit(this.type.name);

      let data = { type: this.type, warehouse: this.catalogMode };
      this.productList = [];
      this.productList = await fetch(
        this.$connect + "catalog/productList.php",
        {
          method: "POST",
          body: JSON.stringify(data),
        }
      ).then((resp) => resp.json());
    },
    async catlogList() {
      this.productList = [];
      let data = { type: this.type, warehouse: this.catalogMode };
      this.productList = await fetch(
        this.$connect + "catalog/productList.php",
        {
          method: "POST",
          body: JSON.stringify(data),
        }
      ).then((resp) => resp.json());
      if (!this.catalogMode) {
        this.productType.push({ name: "", selected: false });
      }
    },
    getImgUrl(pic) {
      return require(pic);
    },
    async openPreview(id) {
      if (this.type == "Linoleum") {
        this.previewProduct = await fetch(
          this.$connect + "catalog/previewProduct.php?id_linoleum=" + id
        ).then((resp) => resp.json());
        this.preview = true;
      } else if (this.type == "Plintusa") {
        this.previewProduct = await fetch(
          this.$connect + "catalog/previewProduct.php?id_plinth=" + id
        ).then((resp) => resp.json());
        this.preview = true;
      }
    },
    closePreview() {
      this.previewProduct = {};
      this.preview = false;
      let type = this.productType.filter(
        (type) => translit(type.name) == this.type
      )[0];
      this.selectType(type);
    },
    addPrice(s) {
      if (!s) return false;
      if (s < 11)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p0_10;
      else if (s < 16)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p11_15;
      else if (s < 21)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p16_20;
      else if (s < 31)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p21_30;
      else if (s < 41)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p31_40;
      else if (s < 56)
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p41_55;
      else
        this.previewProduct.price.selected =
          this.previewProduct.price.all.p56_70;
    },
  },
  props: {
    add: Boolean,
    catalog: Boolean,
  },
  emits: ["addProduct"]["close"],
  mounted: function () {
    // Close modal with 'esc' key
    document.addEventListener("keydown", (e) => {
      if (e.keyCode == 27) {
        this.$emit("close");
      }
    });
    this.$nextTick(function () {
      this.previewAdd = Boolean(this.add);
      this.catalogMode = Boolean(this.catalog);
      this.catlogList();
    });
  },
};
</script>
<style lang="scss" scoped src="./../../assets/styles/catalog.scss"></style>
